@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&family=EB+Garamond);
body {
  width: 100%;
  overflow-x: hidden;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  margin: 0;
  background: #ffffff;
}

::selection {
  background: #000;
  color: #ffffff;
}

::-webkit-input-placeholder {
  background: #ffffff;
  color: #333333;
  font-size: 15px;
}

:-ms-input-placeholder {
  background: #ffffff;
  color: #333333;
  font-size: 15px;
}

::-ms-input-placeholder {
  background: #ffffff;
  color: #333333;
  font-size: 15px;
}

::placeholder {
  background: #ffffff;
  color: #333333;
  font-size: 15px;
}

body.modal-open {
  overflow: scroll !important;
}

i {
  cursor: pointer;
}

a,
a:hover,
a:active,
a:focus {
  -webkit-text-decoration-color: none !important;
          text-decoration-color: none !important;
  text-decoration: none !important;
  outline: none;
}

a.disabled {
  pointer-events: none;
}

.form-control {
  outline: 4 !important;
  box-shadow: none !important;
  border-radius: 0px !important;
  font-size: 12px;
}

.btn {
  box-shadow: none !important;
  border-radius: 1.5px !important;
}

/** My NavBar **/

.MyNavbar {
  background: #ffffff !important;
  height: 55px;
  z-index: 10;
  box-shadow: 0px 3px 13px 0px rgba(0, 0, 0, 0.2);
  position: -webkit-sticky;
  position: -moz-sticky;
  position: -ms-sticky;
  position: -o-sticky;
  position: sticky;
  top: 0px;
}

.MyNavbar .nav-link {
  font-size: 0.9em;
  color: #eb7404 !important;
  font-weight: bold;
  padding-left: 35px;
  padding-right: 35px;
  text-align: center;
}

.MyNavbar .nav-link a {
  color: #eb7404 !important;
}

.MyNavbar .nav-link:hover {
  color: #000000 !important;
}

.MyNavbar-fluid {
  padding: 0 60px;
}

.MyNavbar .navbar-brand {
  margin-top: 0px;
}

/* Typograph */

h1,
h2,
h3,
h4,
h5,
h6,
th {
  font-family: serif, "Crimson Text";
}

p,
span,
td,
li,
ul {
  font-family: sans-serif, serif;
}

.text-capitalize {
  text-transform: capitalize;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-lowercase {
  text-transform: lowercase;
}

.text-bold {
  font-weight: bold;
}

i {
  cursor: pointer;
}

