.center-text {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-weight {
  font-weight: 400;
}

/* My Navbar Drop down */

.MyNavbar .dropdown-menu {
  box-shadow: none !important;
  border-top: 50% !important;
  border-left: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
}

.MyNavbar .dropdown-menu .dropdown-item {
  font-size: 0.9em;
  color: #eb7404 !important;
  font-weight: bold;
  padding-left: 20px;
  padding-right: 50px;
  letter-spacing: 1px;
}

.MyNavbar .dropdown-menu .dropdown-item a {
  color: #eb7404 !important;
}

.MyNavbar .dropdown-menu .dropdown-item:hover,
.MyNavbar .dropdown-menu .dropdown-item:active,
.MyNavbar .dropdown-menu .dropdown-item:focus {
  background: none;
}

.MyNavbar .navbar-toggler {
  background: trasparent;
  outline: none;
  border-style: none;
}

@media (max-width: 1280px) {
  .MyNavbar {
    background: #ffffff !important;
    height: 50px;
    z-index: 10;
    box-shadow: 0px 3px 13px 0px rgba(0, 0, 0, 0.08);
  }
}

@media (max-width: 780px) {
  .MyNavbar {
    background: #ffffff !important;
    height: 80px;
    z-index: 10;
    box-shadow: 0px 3px 13px 0px rgba(0, 0, 0, 0.08);
    align-items: center;
    position: -webkit-sticky;
    position: -moz-sticky;
    position: -ms-sticky;
    position: -o-sticky;
    position: sticky;
    top: 0px;
    width: 100%;
  }

  .MyNavbar .navbar-collapse {
    background: #ffffff;
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    min-height: 60%;
    border-top: 1px;
  }

  .MyNavbar .navbar-toggler {
    position: absolute;
    right: 10px;
    top: 40%;
    font-size: 17px;
  }
  body {
    width: 100%;
  }
}

/* Important Css */

.MyNavbar {
  /* */
}

.MyNavbar .Container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center !important;
}

.MyNavbar .Navbar-brand a {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  text-decoration: none;
  -webkit-text-decoration-color: none !important;
          text-decoration-color: none !important;
  text-decoration: none !important;
  outline: none;
  align-self: center;
}

.MyNavbar .Navbar-brand a p {
  font-size: 1em !important;
  color: black !important;
  font-weight: bold;
  text-transform: uppercase !important;
  text-align: center;
  margin-top: 20px;
}

.MyNavbar .Navbar-brand a img {
  align-self: center;
}

.news-letter {
